.m-tab{
  cursor: pointer;
  @include center;
  height: 2.75em;
  border-bottom: $border;
  transition: background-color .3s, color .3s;
  &._active{
    border-color: $primary;
    background: $brand100;
  }
  &._disabled{
    border-color: $surface;
    color: $borderColor;
    background: transparent;
    pointer-events: none;
  }
  &:hover{
    background: $brand50;
    border-color: $primary;
  }
}